import React, {useState, useEffect} from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import './Navbar.css';
import logo from '../images/logo.png';
import linkedin from '../images/linkedin.svg';
import github from '../images/github.svg'
import { useLocation } from 'react-router';
function MyNavbar() {
  const [select, setSelect] = useState('work');
  const [navBg, setNavBg] = useState({height:'10%', width:'100vw',minWidth: '100%'})
  let location = useLocation().pathname.slice(1);

  useEffect(() => {
    console.log(location);
    const navbarBg = {height:'10%', width:'100vw', minWidth: '100%',backgroundColor:'#F8DFDB'};
    if (location === 'about' || location === 'contact') {
      setSelect(location);
      setNavBg(navbarBg);
    } else if (location !== 'work') {
      setNavBg(navbarBg);
    }
    if(location === 'work' || !location) {
      setNavBg({height:'10%', width:'100vw',minWidth: '100%'})
    }
  }, [location])

  return (
      <Navbar variant="light" expand="lg" sticky="top" style={navBg}>
        <Navbar.Brand href="/" color="light" style={{margin:'auto',width:'80%', fontFamily:"Inknut Antiqua"}}>
          <img height={40} width={40} alt="logo" style={{marginBottom:'1%', marginRight:'1%'}} src={logo}/>
          Yuchen
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav"/>
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            {['work', 'about', 'contact'].map((btn, i) =>
             <Nav.Link
               key={i}
               className={select === btn && "active"}
               href={`/${btn}`}
              >{btn.toUpperCase()}</Nav.Link>
            )}
          <Nav.Link style={{marginRight:'-5%'}} href="https://www.linkedin.com/in/yuchen-rain-pan" >
              <img height={40} width={40} src={linkedin} alt="linkedin" title="LinkedIn"/>
          </Nav.Link>
          <Nav.Link href="https://github.com/pyc0422" >
              <img height={40} width={40} src={github} alt="github" title="github"/>
          </Nav.Link>


          </Nav>
        </Navbar.Collapse>
      </Navbar>
  );
}

export default MyNavbar;
