
import React, {useState} from 'react';
import Footer from './components/Footer';
import MyNavbar from './components/NavBar.js';
import Home from './components/Home';
import About from './components/About';
import Contact from './components/Contact';
import Project from './components/Project';
import biteshare from './images/biteshare.png';
import backend from './images/backend.jpg';
import frontend from './images/frontend.jpg'
import { Routes, Route } from 'react-router-dom';
import {Container} from 'react-bootstrap';
import bgflower from './images/bgflower.gif';
import stock from './images/stock.webp';
const bgImgs = {
  'BiteShare':biteshare,
  'Atelier-backend': backend,
  'Atelier-frontend': frontend,
  'mockStockMarket': stock,
  'neway-receipt-project': 'blue'
}

const bgStyle = {
  minHeight: '100%',
  height: '100vh',
  overflow: 'auto',
  backgroundSize:'cover'
}
function App() {
  const [isHover, setHover] = useState({status: false, item:0});
  const [clicked, setClicked] = useState({btn:'work', project:null});
  const handleClicked = (btn, project) => {

    let selectProject = !project ? null : project;
    if (project) {
      setHover({status:false, item:0})
    }
    setClicked({btn:btn, project:selectProject})
  }

  const handleHover = (_isHover, i) => {
    setHover({status: _isHover, item: i});
  }
  //hover name change the bg image
  const divStyle = !isHover.status ? {...bgStyle, backgroundColor: '#F8DFDB', backgroundImage:`url(${bgflower})`, backgroundRepeat:'repeat' } : {...bgStyle, backgroundImage:`url(${bgImgs[isHover.item]})`};

  return (
    <Container fluid style={divStyle}>
      <MyNavbar clicked={clicked} handleClicked={handleClicked}/>
      <Routes>
        {["/", "/work"].map((p, i) =>
          <Route path={p} key={i} element={<Home isHover={isHover} handleHover={handleHover} handleClicked={handleClicked} />} />
        )}
        <Route path="/about" element={<About />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/project" element={<Project />} />
      </Routes>
      <Footer />
      {/* <p id="footer" >
      {'© Yuchen '}{new Date().getFullYear()}
    </p> */}
    </Container>

  );
}

export default App;
