import {useState, useEffect} from 'react';
import {Form, Button, Spinner} from 'react-bootstrap';
import { Alert, AlertTitle } from '@mui/material'
import { Navigate } from 'react-router-dom';
import './contact.css'

const init_input = {name:'', email:'', msg:''};
const init_alert = {variant:'', area:'', msg:''};
function Contact() {
  const [input, setInput] = useState(init_input);
  const [alert, setAlert] = useState(init_alert)
  const [redirect, setRedirct] = useState(null);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    if (alert.variant.length > 0) {
      setTimeout(()=>{
        setAlert(init_alert)
      }, 3000)
    }
  }, [alert.variant])
  const handleChange = (e) => {
    setInput({...input, [e.target.name]:e.target.value.trim()})
  }
  const sendMessage = (serviceId, templateId, variables) => {
    window.emailjs.send(
      serviceId, templateId, variables
    )
    .then(res => {
      console.log('send');
      setInput(init_input);
      setLoading(false)
      setAlert({...alert, variant:'success', msg:'Thank you for reaching out.'});
      setTimeout(() => {
        setRedirct(true);
      },2000)
    })
    .catch(err => {
      setLoading(false)
      setAlert({...alert, variant:'error', msg:'Sorry. We cannot send email for the moment. Please try again later.'})
    });
  }
  const isEmail = (email) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email);
  const handleSubmit = (e) => {
    e.preventDefault();
    const templateId = "template_p7b8iej";
    const serviceId="service_pw9rij5";
    if (!input.name) {
      setAlert({variant:'error', area:'name', msg: 'Please provide your name.'});
      return
    } else if (!input.email) {
      setAlert({variant:'error', area:'email',msg: 'Please provide your email address.'});
      return
    } else if (!input.msg) {
      setAlert({variant:'error', area:'msg', msg: 'Please fill in the message.'});
      return
    } else if (!isEmail(input.email)){
      setAlert({variant:'error', area:'email', msg:'Please provide valid email'});
    }else{
      setLoading(true)
      sendMessage(serviceId, templateId, { from_name: input.name, email:input.email, message:input.msg})
    }

  }
  if (redirect) {
    return <Navigate to='/' replace={true} />
  }

  return (
    <div style={{width:'90%',margin:'auto', padding:'5%'}}>
       <h2 style={{textAlign:'center', marginBottom:'2%'}}>Contact me</h2>
      {alert.variant === 'success' &&
      <Alert severity="success" onClose={() => setAlert(init_alert)}>
        <AlertTitle>{alert.variant}</AlertTitle>
        {alert.msg}
      </Alert>}
      {loading && <Spinner style={{display:'flex', margin:'auto'}} animation="border" variant="primary"/>}
      {alert.variant === 'error' && <Alert onClose={() => setAlert(init_alert)} severity='error' sx={{mb:2}}>{alert.msg}</Alert>}
      {(alert.variant !== "success" && !loading) &&
      <Form id='myForm'>

        <div id="guest-info">
          <Form.Group className="mb-3" controlId="formGridname">
            <Form.Label>Your Name</Form.Label>
            <Form.Control isInvalid={alert.area === 'name'} type="name" name="name" placeholder="Enter Your Name" onChange={handleChange}/>
          </Form.Group>
          <Form.Group className="mb-3" controlId="email" onChange={handleChange}>
            <Form.Label>Email address</Form.Label>
            <Form.Control isInvalid={alert.area === 'email'} type="email" name="email" placeholder="Enter email" />
          </Form.Group>
        </div>
        <div id="email-msg">
        </div>
        <Form.Group>
          <Form.Label>Content</Form.Label>
          <Form.Control isInvalid={alert.area === 'msg'} onChange={handleChange} name="msg" as="textarea" rows={5} placeholder="Message..."/>
        </Form.Group>
        <div style={{textAlign:'center', margin:'4% auto'}}>
          <Button onClick={handleSubmit} variant="primary" type="submit">
            Send
          </Button>
        </div>
      </Form>
    }
    </div>

  )
}

export default Contact;