import React from 'react';
import { Container, Nav } from 'react-bootstrap';
import './Home.css';
import projects from './projects.js';
//import Footer from './Footer';

function Home({isHover, handleHover, handleClicked}) {
  return (
    <Container style={{textAlign:'center'}}>
        {/* <img id="heart" width={70} height={70} src="https://i.gifer.com/NRI0.gif" alt="hello" /> */}
        <h1 style={{marginTop:'2%'}}>
          Hello, I'm Yuchen Pan
        </h1>
        <br/>
        <h5 style={{fontWeight:300}}>
          Welcome to my personal website! Here you can learn more about me and my work.
        </h5>
        <br/>
        <div className="separator">My work</div>
        <nav className='nav-section'>
            {projects.map((project, i) =>
                <Nav.Link
                  className={`project-item ${i}`}
                  onMouseOver={() => handleHover(true, project.name)}
                  onMouseLeave={() => handleHover(false, 0)}
                  onClick={() =>handleClicked('project', project)}
                  key={i}
                  href={`/project?id=${i + 1}`}
                  style={{marginBottom:'2%'}}
                >
                  <h2>{project.showName || project.name}</h2>
                </Nav.Link>
            )}
        </nav>
        {/* <Footer component="home" /> */}
    </Container>
  );
}

export default Home;