import { Button } from 'react-bootstrap';
import { useLocation } from 'react-router';

function Footer () {
  let location = useLocation().pathname;
  return (
    <div style={{textAlign:'center'}}>
      {location === '/work'|| location === "/" ?
      (<div id="buttons">
        <Button href="https://docs.google.com/document/d/1WAeQwufi4KDar42VdbDMyeywFEoWkmri6z69kMffGr4/edit?usp=sharing">Resume</Button>
        <Button style={{marginLeft:'5%'}} href="/contact" variant="warning">Contact</Button>
      </div>)
      :
      <Button href="/" style={{marginBottom:'2%',fontSize:'larger', color:'black', backgroundColor:'transparent', border:'none', textDecoration:'underline'}}>{'>> Back'}</Button>
     }
     <p id="footer" >
      {'© Yuchen '}{new Date().getFullYear()}
    </p>
    </div>

 )
}

export default Footer;